


import "./index.scss";



import { NavBar, Icon } from "antd-mobile";

import { Modal, Button,Drawer } from 'antd';

import {history} from "&";

export default class Head extends Component {

    constructor(){
        super();
        this.state ={
            visible:false,
            visible2:false
        }
    }

    showModal = () => {
        this.setState({
          visible: true,
        });
      };
    
      handleOk = e => {
        console.log(e);
        this.setState({
          visible: false,
        });
        history.push("/login")
      };
    
      handleCancel = e => {
        console.log(e);
        this.setState({
          visible: false,
        });
       
      };
      showDrawer = (e) => {
        this.setState({
          visible2: true,
        });
    };

    
      onClose = () => {
        this.setState({
          visible2: false,
        });
      };
    render() {

        return (

            <NavBar

                style={{ height: "100%", width: "100%", backgroundColor: "#222" }}
                onLeftClick={() => console.log('onLeftClick')}
                rightContent={[
                    <Icon type="smile" theme="twoTone" key={5}/>,
                    <span key={1} onClick={this.showDrawer}>管理员</span>,
                    <span className="quit" onClick={this.showModal} key={2}>退出</span>,
                    <Modal
                    key={3}
                    title="退出登录"
                    visible={this.state.visible}
                    onOk={this.handleOk}
                    onCancel={this.handleCancel}
                  >
                    <p>你确定要退出吗？</p>
                  </Modal>,
                  <Drawer
                  key={4}
                  title="用户信息"
                  placement="right"
                  closable={false}
                  onClose={this.onClose}
                  visible={this.state.visible2}
                >
                   <ul>
                       <li>管理员</li>
                       <li>手机号</li>
                       <li>单位</li>
                       <li>用户角色</li>
                       <li>修改密码</li>
                   </ul> 
                </Drawer>
                ]}
            >
            xxx公司员工后台管理系统
            </NavBar>
        )
    }

}