

import "./index.scss"


import { Button, Layout } from 'antd'

import Lazyload from "&/lazyload";
import { Switch, Route, Redirect } from "react-router-dom";
import Head from "~/components/Head"

import PCSider from "~/components/Sider";

export default class Index extends Component {
    render() {
        return (
            <div className="main">
                <Layout style={{ width: "100%", height: "100%", color: "white", backgroundColor: "#222" }}>
                    <Layout.Header style={{ height: "10%", backgroundColor: "#222" }}>
                        <Head></Head>
                    </Layout.Header>
                    <Layout style={{ height: "100%" }}>
                        <Layout.Sider width="15%">
                            <PCSider>
                            </PCSider>
                        </Layout.Sider>
                        <Layout.Content>
                            <Switch>
                                <Route path="/index/overview" component={Lazyload(() => import("../overview"))}></Route>
                                <Route path="/index/socket" component={Lazyload(() => import("../socket"))}></Route>
                                <Route path="/index/chart" component={Lazyload(() => import("../chart"))}></Route>
                                <Route path="/index/setting" component={Lazyload(() => import("../setting"))}></Route>
                                <Route render={() => (<Redirect to="/index/overview"></Redirect>)}></Route>
                            </Switch>
                        </Layout.Content>

                    </Layout>


                </Layout>
            </div>
        )
    }
}