




import "./index.scss";

import { Menu, Icon, Switch } from "antd";

import { history } from "&"

const { SubMenu } = Menu;

export default class PCSider extends Component {
    constructor() {
        super();
        this.state = {
            theme: 'dark',
            current: '1',
            ms: [
                { txt: "概览", key: "1", icon: "appstore", path: "/index/overview" },
                { txt: "员工状态管理", key: "2", icon: "user-add", path: "/index/socket" },
                { txt: "员工档案", key: "3", icon: "file", path: "/index/chart" },
                { txt: "设置", key: "5", icon: "setting", path: "/index/setting/usermanage" },
            ]
        }
    }
    changeTheme = value => {
        this.setState({
            theme: value ? 'dark' : 'light',
        });
    };

    handleClick = (e) => {
        this.setState({
            current: e.key,
        });
    }
    select = (item) => {
        if (item.key == 1) {
            history.push("/index/setting/usermanage")
        } else if (item.key == 2) {
            history.push("/index/setting/rolemanage")
        } else {
            history.push("/index/setting/jurisdictionmanage")
        }
    }
    //点击二级子菜单  
    //点击 一级 菜单
    titleClick = (item) => {
        history.push(item.path)
    }
    render() {
        const { ms } = this.state;
        return (
            <div>
                <Switch
                    checked={this.state.theme === 'dark'}
                    onChange={this.changeTheme}
                    checkedChildren="Dark"
                    unCheckedChildren="Light"
                />
                <br />
                <br />
                <Menu
                    onSelect={this.select}
                    subMenuCloseDelay={2}
                    theme={this.state.theme}
                    onClick={this.handleClick}
                    style={{ width: "100%" }}
                    defaultOpenKeys={['sub1']}
                    defaultSelectedKeys={['sub1']}
                    selectedKeys={[this.state.current]}
                    mode="inline"
                >
                    {
                        ms.map((item, index) => {
                            return (
                                <SubMenu
                                    onTitleClick={() => this.titleClick(item)}
                                    key={index}
                                    path={item.path}
                                    title={
                                        <span>
                                            <Icon type={item.icon} />
                                            <span>{item.txt}</span>
                                        </span>
                                    }
                                >
                                    <Menu.Item style={{ display: item.key == 5 ? "block" : "none" }} key="1">员工列表</Menu.Item>
                                    <Menu.Item style={{ display: item.key == 5 ? "block" : "none" }} key="2">员工管理</Menu.Item>
                                    <Menu.Item style={{ display: item.key == 5 ? "block" : "none" }} key="3">角色管理</Menu.Item>
                                </SubMenu>
                            )
                        })
                    }
                </Menu>
            </div>
        )
    }
}